<template>
  <div class="body-bg">
    <headerBar :header-data="headerData" />
    <div class="login-h">注册</div>
    <div class="login-h-mini">欢迎注册账号</div>
    <div class="login-form">
      <van-row>
        <van-col span="6">
          <van-radio v-model="checked" name="0">境内</van-radio>
        </van-col>
        <van-col span="6">
          <van-radio v-model="checked" name="1">境外</van-radio>
        </van-col>
      </van-row>
      <p v-if="zcdts" class="input-item-msg">请选择注册地</p>

      <van-cell-group>
        <div class="input-item">
          <van-field
            ref="name"
            v-model="ztmc"
            v-validate="'required'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请按营业执照填写公司全称"
            title="公司全称"
            name="ztmc"
          />
          <p class="input-item-msg">{{ errors.first('ztmc') }}</p>
        </div>

        <div class="input-item">
          <van-field
            ref="name"
            v-model="ztdm"
            v-validate="'required|xydmLength'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请按营业执照填写统一社会信用代码"
            title="统一社会信用代码"
            name="ztdm"
          />
          <p class="input-item-msg">{{ errors.first('ztdm') }}</p>
        </div>
        <div class="input-item password">
          <van-field
            ref="id"
            v-model="mm"
            v-validate="'required|mmLength'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入登录密码"
            title="登录密码"
            :type="eyesChange ==true?'text':'password'"
            name="mm"
            @blur="checkMm()"
          />
          <!-- 显示与隐藏密码 -->
          <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconkejian" />
          </div>
          <div v-else class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconbukejian" />
          </div>
          <p class="input-item-msg">{{ errors.first('mm') }}</p>
        </div>
        <div class="input-item password">
          <van-field
            ref="id"
            v-model="cfmm"
            v-validate="'required'"
            clearable
            is-highlight
            is-title-latent
            placeholder="重复密码"
            title="再次输入密码"
            :type="eyesChange ==true?'text':'password'"
            name="cfmm"
            @blur="checkMm()"
          />
          <!-- 显示与隐藏密码 -->
          <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconkejian" />
          </div>
          <div v-else class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconbukejian" />
          </div>
          <p v-if="mmtsState" class="input-item-msg">两次密码不一致</p>
          <p v-if="!mmtsState" class="input-item-msg">{{ errors.first('cfmm') }}</p>
        </div>
        <div class="input-item">
          <van-field
            ref="name"
            v-model="lxr"
            v-validate="'required'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入联系人"
            title="联系人"
            name="lxr"
          />
          <p class="input-item-msg">{{ errors.first('lxr') }}</p>
        </div>

        <div class="input-item">
          <van-field
            ref="name"
            v-model="lxrdh"
            v-validate="'required|phone'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入联系人手机号码"
            title="联系人手机号码"
            name="lxrdh"
            @keyup="checkSjh()"
          />
          <p v-if="sjhState" class="input-item-msg">手机号已注册</p>
          <p v-if="!sjhState" class="input-item-msg">{{ errors.first('lxrdh') }}</p>

          <div class="input-item">
            <van-field
              ref="id"
              v-model="identifyCode"
              v-validate="'required'"
              clearable
              is-highlight
              is-title-latent
              placeholder="请输入验证码"
              title="验证码"
              name="yzm"
              @blur="checkYzm()"
            />
            <div class="input-operator mini">
              <van-button v-show="!computedTime" plain round size="mini" type="info" @click.prevent="getVerifyCode">
                获取验证码
              </van-button>
              <van-button v-show="computedTime" plain round size="mini" type="info" @click.prevent>
                已发送({{ computedTime }}s)
              </van-button>
            </div>
            <p v-if="yzmts" class="input-item-msg">验证码错误，请重新输入</p>
            <p v-if="!yzmts" class="input-item-msg">{{ errors.first('yzm') }}</p>
          </div>
        </div>
        <div class="input-item">
          <van-field
            ref="name"
            v-model="email"
            v-validate="'required|email'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入联系人邮箱"
            title="联系人邮箱"
            name="lxryx"
          />
          <p class="input-item-msg">{{ errors.first('lxryx') }}</p>
        </div>
        <div class="input-item">
          <van-field
            ref="name"
            v-model="ggemail"
            v-validate="'required|email'"
            clearable
            is-highlight
            is-title-latent
            placeholder="用于密码找回，请使用公共邮箱"
            title="公共邮箱"
            name="ggyx"
          />
          <p class="input-item-msg">{{ errors.first('ggyx') }}</p>
        </div>

        <div class="agreement-item flex">
          <van-checkbox v-model="radio" :disabled="disabled" @click="isRead()" />
          <div class="text">我已认真阅读并同意 <span class="blue" @click="goGysZcxy()">《服务协议》</span></div>
        </div>
        <p v-if="fwxyts" class="input-item-msg">请阅读并同意服务协议</p>

        <div class="login-button">
          <van-button round type="info" @click="handleSubmit()">注册</van-button>
        </div>
      </van-cell-group>
    </div>
    <div class="common-space" />
  </div>

</template>

<script>
import headerBar from '@/components/header/headerBar'
// import alertTip from '../../../components/common/alertTip'
// import SIdentify from '../../../components/identify/identify' //本地图片验证码
import { mapActions } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'
import { checksjh } from '@/store/module.js'
export default {
  name: 'GysZc',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      yzmts: false,
      fwxyts: false,
      zcdts: false,
      mmtsState: false,
      sjhState: false,
      disabled: true, // 是否阅读服务协议
      radio: false, // 是否选中服务协议
      checked: 2, // 选择供应商来源
      eyesChange: false, // 是否展示密码
      profiletitle: '',
      computedTime: 0, // 倒数记时
      ztmc: '', // 公司名称
      ztlb: '', // 法人类型(1-自然人21-供应商22-代理机构23-采购人)
      ztdm: '', // 供应商代码
      lxr: '', // 联系人
      lxrdh: '', // 联系电话
      mm: '', // 密码
      cfmm: '', // 重复密码
      email: '', // 邮箱
      ggemail: '', // 公共邮箱
      captchaCodeImg: '', // 验证码地址
      codeNumber: '', // 返回验证码
      phoneNumber: '', // 手机号
      identifyCodes: '1234567890', // 本地验证码
      identifyCode: '', // 本地验证码
      headerData: {
        title: '注册',
        leftUrl: 'back',
        leftText: '登录'
      }
    }
  },

  created() {
    this.getCaptchaCode()
  },
  methods: {
    ...mapActions([
      'zc', 'sendMessage'
    ]),
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    checkMm() {
      if (this.mm !== this.cfmm) {
        this.mmtsState = true
      } else {
        this.mmtsState = false
      }
    },
    checkYzm() {
      if (this.codeNumber !== this.identifyCode) {
        this.yzmts = true
      } else {
        this.yzmts = false
      }
    },
    isRead() {
      if (this.disabled === true) {
        this.$toast('请先阅读协议')
      } else {
        return
      }
    },
    checkSjh() {
      if (!(/^1[3456789]\d{9}$/.test(this.lxrdh))) {
        return false
      }
      checksjh.data = this.lxrdh
      this.asyncApiModule(checksjh).then(res => {
        if (res === true) {
          this.sjhState = false
          return false
        } else {
          this.sjhState = true
          return true
        }
      })
    },

    /* 供应商注册*/
    handleSubmit() {
      this.$validator.validate().then(result => {
        if (this.codeNumber !== this.identifyCode) {
          this.yzmts = true
        } else {
          this.yzmts = false
        }
        if (this.checked === 2) {
          this.zcdts = true
        } else {
          this.zcdts = false
        }
        if (this.radio === false) {
          this.fwxyts = true
        } else {
          this.fwxyts = false
        }
        if (this.zcdts === true || this.fwxyts === true || this.yzmts === true) {
          return false
        }
        if (!result) {
          // this.$toast.fail('数据格式有误')
          // this.$message.warning(this.errors.all()[0])
        } else {
          this.zc({
            ztlb: '21',
            zcd: this.checked,
            ztmc: this.ztmc,
            ztdm: this.ztdm,
            lxr: this.lxr,
            lxrdh: this.lxrdh,
            mm: this.mm,
            email: this.email
          }).then((res) => {
            this.$toast.fail('注册成功')
            this.$router.push({
              name: 'GysZcSuccess'
            })
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },

    // 获取短信验证码
    getVerifyCode() {
      if (!(/^1[3456789]\d{9}$/.test(this.lxrdh))) {
        return false
      }
      if (this.sjhState === true) {
        return false
      }
      this.computedTime = 60
      this.timer = setInterval(() => {
        this.computedTime--
        if (this.computedTime === 0) {
          clearInterval(this.timer)
        }
      }, 1000)
      this.sendMessage({
        sjh: this.lxrdh
      }).then(res => {
        this.codeNumber = res
      }).catch(error => {
        console.log(error)
      })
    },
    // 获取验证吗，线上环境使用固定的图片，生产环境使用真实的验证码
    getCaptchaCode() {
      // const res = await getcaptchas()
      // this.captchaCodeImg = res.code
    },
    // 随机数
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min)
    },
    // 刷新数据
    refreshCode() {
      this.identifyCode = ''
      this.makeCode(this.identifyCodes, 4)
    },
    // 产生数据
    makeCode(o, l) {
      // for (let i = 0; i < l; i++) {
      //   this.identifyCode += this.identifyCodes[
      //     this.randomNum(0, this.identifyCodes.length)
      //   ]
      // }
      // console.log(this.identifyCode)
    },

    /* 控制是否展示密码*/
    eyesCheck() {
      this.eyesChange = !this.eyesChange
    },
    eyesCheck1() {
      this.eyesChange1 = !this.eyesChange1
    },
    // 关闭弹出框
    // closeTip() {
    //   this.showAlert = false;
    // },
    goGysZcxy() {
      this.disabled = false
      this.$router.push({
        name: 'GysZcxy'
      })
    }

  }

}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
